.btn {
  composes: btn from "bootstrap/dist/css/bootstrap.css";
  background-color: #A2BCF2;
}

.btn_secondary {
  composes: btn from "bootstrap/dist/css/bootstrap.css", btn-secondary from "bootstrap/dist/css/bootstrap.css";
  background-color: gray;
}

.btn,
.btn_secondary {
  display: block;
}

@media (min-width: 576px) {
  .btn,
  .btn_secondary {
    display: flow-root;
  }
}
